<template>
    <section>
      <div class="markdown" v-html="content"></div>
    </section>
</template>

<script>
export default {
  name: "blogContent",
  data() {
    return {
      content: ""
    };
  },
  beforeRouteUpdate(to, from, next) {
    next(() => {
      console.log("zzz");
    });
  },
  created() {
    let item = JSON.parse(sessionStorage.getItem("nowContent"));
    this.$axios
      .get("../../static/article/" + item.url)
      .then(data => {
        let text = this.$marked(data.data);
        this.content = text;
      })
      .catch(function(err) {
        console.log("error");
      });
  },
  mounted() {},
  updated() {
    setTimeout(this.$hljs.highlightCode, 0);
  },
  methods: {}
};
</script>


<style scoped>
section {
  width: 100%;
  text-align: left;
  background-color: #fff;
}
.markdown{
  padding:30px;
}
</style>


